import Layout from '../components/Layout';
import Masonry from 'react-masonry-css';

export default () => (
  <Layout>

    <section>
      <h1>Our Services</h1>

      <Masonry
        breakpointCols={3}
        className="my-masonry-grid"
        columnClassName="my-masonry-grid_column"
      >
        {() => {
          for (let i = 1; i <= 15; i++) {
            return <img src={`../static/img/services-gallery/${i}.jpg`} alt="" />
          }
        }}
      </Masonry>
    </section>

    <style jsx>
      {`
        section {
          flex: 1
          padding: 1rem
          text-align: center
        }
      `}
    </style>
  </Layout>
);



